@import "settings/colors";
@import "settings/variables";
@import "settings/resetStyle";
//navigation
.containerBox {
  @media (min-width: $desktop) {
    display: flex;
    max-width: 1920px;
    width: 100vw;
  }
}

.navigation {

  @media (min-width: $desktop) {
    display: flex;
    width: 100vw;
    height: 80px;
    border-bottom: 1px solid black;
    box-shadow: 0 1px 21px rgba(0, 0, 0, 0.0520088);
  }

  &__list {
    overflow: hidden;
    background-color: $color-white;
    @media (min-width: $desktop) {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 80px;
    }
  }

  a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }

  &__elementList {
    &:hover {
      background-color: $color-buttons;
      color: $color-white;
      border-radius: 10px;

      a {
        color: $color-white
      }
    }
  }

}

.navigation__list a.active {
  background: url("../img/l.png");
  width: 48px;
  height: 47px;
  display: block;
}


.navigation__list li {
  display: none;
}

.navigation__list a.icon {
  float: right;
}

.navigation__list.responsive .icon {
  position: absolute;
  right: 0;
  top: 0;
}

@media screen and (min-width: $desktop) {
  .navigation__list li {
    display: block;
  }
  .navigation__list a.icon {
    display: none
  }

}

@media screen and (max-width: $desktop) {
  .navigation__list.responsive {
    position: relative;
  }
  .navigation__list.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  .navigation__list.responsive li {
    float: none;
    display: block;
    text-align: left;
  }
}


